.myskills {
  text-align: center;
  margin-top: 5rem;
  gap: 2rem;
  &__content {
    display: grid;
    place-items: center;
    &__content1 {
      display: flex;
      justify-content: center;
      gap: 1rem;
      &__html,
      &__css,
      &__react {
        & > img {
          height: 10rem;
          width: 10rem;
        }
      }
    }
    &__content2 {
      display: flex;
      justify-content: center;
      gap: 1rem;
      &__java,
      &__php,
      &__python {
        & > img {
          height: 10rem;
          width: 10rem;
        }
      }
    }
  }
}
@media only screen and (min-width: 768px) {
  .myskills__content__content1 {
    &__html,
    &__css,
    &__react {
      & > img {
        height: 10rem;
        width: 10rem;
      }
    }
  }
  .myskills__content__content2 {
    &__java,
    &__php,
    &__python {
      & > img {
        height: 10rem;
        width: 10rem;
      }
    }
  }
}
